import { makeStyles } from '@material-ui/core/styles';
import { withTheme } from '@material-ui/core/styles';
import React, { useState } from 'react';
import Table, { CELL_TYPES, FILTER_TYPES } from 'mui-advanced-table';
import PropTypes from 'prop-types';

const ThemedTable = withTheme(Table);

const useStyles = makeStyles(() => ({
  tableSize: {
    width: '100vw',
    height: '83vh',
  },
  rowHover: {
    backgroundColor: 'red',
  },
}));

const makeRow = (checkbox, pickDate, textField, count, date, numberInput, dropdown, text) => ({
  checkbox,
  pickDate,
  textField,
  count,
  date,
  numberInput,
  dropdown,
  text,
});

const testingRows = [];
for (let i = 0; i < 1500; i++) {
  testingRows.push(makeRow(false, '', 'asdf', 10, '10/10/2018', 10, '', 'Cool kids'));
}

const makeTestingColumns = actions => ([{
  width: 54,
  label: '',
  dataKey: 'checkbox',
  type: CELL_TYPES.CHECKBOX,
  inputAction: actions.checkbox,
  sortable: false,
}, {
  flex: 3,
  minWidth: 150,
  maxWidth: 300,
  label: 'Pick Date',
  dataKey: 'pickDate',
  type: CELL_TYPES.DATE_INPUT,
  inputAction: actions.pickDate,
}, {
  flex: 2,
  minWidth: 150,
  maxWidth: 300,
  label: 'Text Field',
  dataKey: 'textField',
  inputAction: actions.textField,
  type: CELL_TYPES.TEXT_INPUT,
  sortable: true,
  draggable: true,
}, {
  flex: 1,
  label: 'Date',
  dataKey: 'date',
  type: CELL_TYPES.DATE,
}, {
  flex: 2,
  minWidth: 150,
  label: 'Number In',
  dataKey: 'numberInput',
  type: CELL_TYPES.NUMBER_INPUT,
  sortable: true,
  inputAction: actions.numberInput,
  totalable: true,
  filter: {
    type: FILTER_TYPES.TEXT
  }
}, {
  flex: 1.5,
  minWidth: 130,
  label: 'Dropdown',
  dataKey: 'dropdown',
  type: CELL_TYPES.DROPDOWN,
  inputAction: actions.dropdown,
  cellProps: {
    choices: ['One', 'Two', 'Three'],
    placeholder: 'choose...',
    allowMultiple: true,
  },
  draggable: true,
  sortable: true,
}, {
  flex: 2,
  label: 'Dataless Column',
  dataKey: 'noData',
  type: CELL_TYPES.TEXT,
  minWidth: 200,
}, {
  flex: 2,
  label: 'Text',
  dataKey: 'text',
  type: CELL_TYPES.TEXT,
  minWidth: 200,
  draggable: true,
}, {
  flex: 1,
  minWidth: 64,
  label: 'Count',
  dataKey: 'count',
  type: CELL_TYPES.NUMBER,
  totalable: true,
  draggable: true,
  sortable: true,
}]);

const TableTesting = () => {
  const classList = useStyles();
  const [rows, setRows] = useState(testingRows);

  const updateRowProperty = propName => (data, rowData) => {
    testingRows[rowData.index] = {
      ...testingRows[rowData.index],
      [propName]: data
    };
  }

  const actions = {
    checkbox: updateRowProperty('checkbox'),
    pickDate: updateRowProperty('pickDate'),
    textField: updateRowProperty('textField'),
    numberInput: updateRowProperty('numberInput'),
    dropdown: updateRowProperty('dropdown')
  };

  const columns = makeTestingColumns(actions);

  return (
    <ThemedTable
      columns={columns}
      rows={testingRows}
      title="Test title"
      size="medium"
      className={classList.tableSize}
    />
  );
};

TableTesting.defaultProps = {};

TableTesting.propTypes = {};

export default TableTesting;